import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import BonCadeau from '../views/BonCadeau.vue';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/boncadeau',
    name: 'BonCadeau',
    component: BonCadeau
  }
]

const router = new VueRouter({
  //mode: 'history',
  //base: process.env.BASE_URL,
  routes
})

export default router
